@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
  }

.bank-app {
  border-bottom: 5px #e1e1e9;
  flex-direction: column;
  margin-top: 64px;
  color: #fff;
}

.bank-search {
  margin-bottom: 64px;
  flex-direction: column;
  
}

.bank-text {
  margin-bottom: 32px;
  border-bottom: 5px #e1e1e9;
}

.bank-input {
  padding-left: 206px; 
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );

  color: #e2e2e2;
}

.bank-input::placeholder {
  color: #e2e2e2;
}
